<style>
  .tab-wraper>.el-tabs>.el-tabs__header {
    margin: 0;
  }
</style>

<template>
  <div>
    <!-- 搜索框 -->
    <div class="search-form-wraper">
      <el-form :model="searchContent" label-position="top">
        <region-city-store @StoreGroupChange="StoreGroupChange"></region-city-store>
        <el-form-item label="活动ID/名称">
          <el-input class="search-input" v-model.trim="searchContent.activitySearchVal" placeholder="活动ID/名称" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="商品分类">
          <el-cascader
            placeholder="请选择分类"
            class="search-input"
            :options="categoryList"
            change-on-select
            size="small"
            clearable
            filterable
            @change="doSearchHandel(true)"
            :props="cateDefaultProps"
            v-model="searchContent.categoryIdList">
          </el-cascader>
        </el-form-item>
        <el-form-item label="SKU编码/商品名称">
          <el-input class="search-input" v-model.trim="searchContent.goodsInfo" placeholder="SKU编码/商品名称" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="预售类型">
          <el-select class="search-input" v-model="searchContent.presaleType" filterable placeholder="请选择预售类型" size="small" @change="doSearchHandel(true)" clearable>
             <el-option
               v-for="item in presaleTypeOptions"
               :key="item.value"
               :label="item.label"
               :value="item.value">
             </el-option>
           </el-select>
         </el-form-item>
         <el-form-item label="周期类型">
          <el-select class="search-input" v-model="searchContent.cycleType" filterable placeholder="请选择周期类型" size="small" @change="doSearchHandel(true)" clearable>
             <el-option
               v-for="item in cycleTypeOptions"
               :key="item.value"
               :label="item.label"
               :value="item.value">
             </el-option>
           </el-select>
         </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>
    
    <!-- 搜索结果 -->
    <!-- 表格 -->
    <div class="table-wraper tab-wraper" v-loading = "loading">
      <el-tabs v-model="activeName"  @tab-click="handleTabClick">
        <el-tab-pane name="all">
          <span slot="label">全部 {{this.topCardCount.all}}</span>
        </el-tab-pane>
        <el-tab-pane name="onShelf">
          <span slot="label">上架中 {{this.topCardCount.onShelfCount}}</span>
        </el-tab-pane>
        <el-tab-pane name="downShelf">
          <span slot="label">已下架 {{this.topCardCount.offShelfCount}}</span>
        </el-tab-pane>
        <my-table ref="myTable" 
        @updateShelfStatus = "updateShelfStatus" 
        @deleteItem="deleteItem"
        @doBatchDelHandel="doBatchDelHandel" @batchUpdateShelfStatus="batchUpdateShelfStatus"></my-table>
      </el-tabs>
    </div>
    <!-- 分页条 -->
    <div class="pagination" v-show="tableData.length > 0">
      <el-pagination
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="[10, 50, 100, 150, 200]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  import MyTable from "./MyTable"
  import RegionCityStore from '@/components/Common/StoreCommon/RegionCityStore'
  const requestUrl = {
    // 获取三方平台店内分类
    getCategoryTree: '/plateform/a/category/getCategoryTree',
    // 预售商品列表
    searchTableData: '/plateform/a/preSale/storeGoods/listPage',
    // 上下架操作
    batchOffShelf: '/plateform/a/preSale/storeGoods/onShelf',
    // 批量删除
    batchDel: '/plateform/a/preSale/storeGoods/del'
  }
  export default {
    data() {
      return {
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        activeName: 'all',
        searchContent: {
          DIFFCITY: true,
          regionCode: Storage.get('regionCode') || '', // 区域ID
          cityId: '', // 城市id
          activityType: 51,
          activitySearchVal: '',
          shelfStatus: -1, // 上下架状态
          storeGroupTypeNo: '',
          storeGroupNo: '',
          storeNo: '', // 门店编号
          categoryId: '',
          categoryIdList: [],
          presaleType: '',
          cycleType: '', // 周期类型  1 短周期   2  长周期
          goodsInfo: '' // 商品编码或者名称
        },
        // 默认分类参数
        cateDefaultProps: {
          children: 'categoryVoList',
          label: 'categoryName',
          value: 'categoryId'
        },
        cycleTypeOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '短周期',
            value: 1
          },
          {
            label: '长周期',
            value: 2
          }
        ],
        presaleTypeOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '单期',
            value: 1
          },
          {
            label: '周期',
            value: 2
          }
        ],
        categoryList: [], // 商品分类id
        tableData: [],
        // 选中的门店预售商品
        checkedList: [],
        // 选项卡顶部内容
        topCardCount: {
          all: 0,
          onShelfCount: 0, // 上架
          offShelfCount: 0 // 下架
        }

      }
    },
    created() {
      // 获取三方平台店内分类
      this.getCategoryId()
    },
    mounted() {
      // 根据用户选择的城市获取数据
      this.doSearchHandel(true)
    },
    components: {
      "my-table": MyTable,
      "region-city-store": RegionCityStore
    },
    methods: {
      StoreGroupChange(params) {
        this.searchContent.regionCode = params.regionCode
        this.searchContent.cityId = params.cityId
        this.searchContent.storeNo = params.storeNo
        this.searchContent.storeGroupTypeNo = params.storeGroupTypeNoList[0]
        this.searchContent.storeGroupNo = params.storeGroupTypeNoList[1]
        this.doSearchHandel(true)
      },
      /**
       * [getCategoryId 三方平台店内分类]
       * @return {[type]} [description]
       */
      getCategoryId() {
        this.$http.post(requestUrl.getCategoryTree, {DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              for(let item of res.data) {
                if(item.categoryVoList && item.categoryVoList.length === 0) {
                  item.categoryVoList = null
                }
              }
              this.categoryList = res.data
            }else {
              this.categoryList = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(三方平台店内分类):", e.message)
        })
      },
      /**
       * [handleTabClick tab点击切换]
       * @return {[type]} [description]
       */
      handleTabClick(_val) {
        if(_val.name == 'all') {
          this.searchContent.shelfStatus = -1
        }else if(_val.name == 'onShelf') {
          this.searchContent.shelfStatus = 0
        }else if(_val.name == 'downShelf') {
          this.searchContent.shelfStatus = 1
        }
        this.activeName = _val.name
        this.doSearchHandel(true)
      },
      /**
       * [changeSize 分页]
       * @param  {[type]} _val [description]
       * @return {[type]}      [description]
       */
      changeSize(value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      changeCurrent(value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel 搜索]
       * @param  {[type]} _param [description]
       * @return {[type]}        [description]
       */
      doSearchHandel(_param) {
        let spliceLen = this.searchContent.categoryIdList.length - 1
        this.searchContent.categoryId = this.searchContent.categoryIdList[spliceLen]
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        this.$http.post(requestUrl.searchTableData, {...this.searchContent, pageSize: this.pageSize, pageNum: this.pageNum}).then(res => {
          this.loading = false
          if(res.code === 0) {
            if(res.data && res.data.goodsList && res.data.goodsList.length) {
              this.tableData = res.data.goodsList
              let _value = {
                tableData: this.tableData
              }
              if(this.$refs.myTable) {
                this.$refs.myTable.setDefaultMesg(_value)
              }
            }else {
              this.tableData = []
              let _value = {
                tableData: this.tableData
              }
              if(this.$refs.myTable) {
                this.$refs.myTable.setDefaultMesg(_value)
              }
            }
            this.topCardCount.all = res.data.allCount || 0
            this.topCardCount.onShelfCount = res.data.onShelfCount || 0
            this.topCardCount.offShelfCount = res.data.offShelfCount || 0
            this.total = res.data.total || 0
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [updateShelfStatus 更新上下架]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      updateShelfStatus(_value) {
        // 预售商品ID集合
        let ids = []
        ids.push(_value.row.id)

        // shelfStatus 上下架状态：0-上架；1-下架
        let shelfStatus = -1
        let shelfStatusName = ''
        if(_value.shelfStatus == 0) {
          shelfStatus = 0
          shelfStatusName = '上架'
        }else if(_value.shelfStatus == 1) {
          shelfStatus = 1
          shelfStatusName = '下架'
        }

        this.$confirm('确定要执行(' + `${shelfStatusName}` + ')操作吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          utils.showLoading()
          this.$http.post(requestUrl.batchOffShelf, {'ids': ids, 'shelfStatus': shelfStatus, DIFFCITY: true}).then(res => {
            utils.clearLoading()
            if(res.code === 0) {
              if(shelfStatus === 0) {
                this.$message.success("上架成功")
              }else if(shelfStatus === 1) {
                this.$message.success("下架成功")
              }
              this.doSearchHandel()
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            utils.clearLoading()
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [deleteItem 删除]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      deleteItem(_value) {
        let idAndActivityTypeArr = []
        let item = {
          id: _value.row.id,
          activityType: _value.row.activityType
        }
        idAndActivityTypeArr.push(item)
        this.$confirm('确定要执行删除操作吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          utils.showLoading()
          this.$http.post(requestUrl.batchDel, {idAndActivityTypeList: idAndActivityTypeArr, DIFFCITY: true}).then(res => {
            utils.clearLoading()
            if(res.code === 0) {
              this.$message.success("删除成功")
              this.doSearchHandel(true)
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            utils.clearLoading()
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [doBatchDelHandel 批量删除]
       * @return {[type]} [description]
       */
      doBatchDelHandel(_value) {
        if(_value.checkedList) {
          this.checkedList = _value.checkedList
        } 
        if(this.checkedList.length === 0) {
          this.$message("请选择商品")
          return 
        }
        // 预售商品ID集合
        let idAndActivityTypeArr = this.checkedList.map(item => {
          return {
            id: item.id,
            activityType: item.activityType
          }
        })
        this.$confirm('确定要执行批量删除操作吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          utils.showLoading()
          this.$http.post(requestUrl.batchDel, {idAndActivityTypeList: idAndActivityTypeArr, DIFFCITY: true}).then(res => {
            utils.clearLoading()
            if(res.code === 0) {
              this.$message.success("批量删除成功")
              this.doSearchHandel(true)
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            utils.clearLoading()
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [batchUpdateShelfStatus 批量上下架]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      batchUpdateShelfStatus(_value) {
        if(_value.checkedList) {
          this.checkedList = _value.checkedList
        } 
        if(this.checkedList.length === 0) {
          this.$message("请选择商品")
          return 
        }
        // 预售商品ID集合
        let ids = this.checkedList.map(item => {
          return item.id
        })
        // shelfStatus 上下架状态：0-上架；1-下架
        let shelfStatus = -1
        let shelfStatusName = ''
        if(_value.shelfStatus == 0) {
          shelfStatus = 0
          shelfStatusName = '上架'
        }else if(_value.shelfStatus == 1) {
          shelfStatus = 1
          shelfStatusName = '下架'
        }

        this.$confirm('确定要执行批量(' + `${shelfStatusName}` + ')操作吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          utils.showLoading()
          this.$http.post(requestUrl.batchOffShelf, {'ids': ids, 'shelfStatus': shelfStatus, DIFFCITY: true}).then(res => {
            utils.clearLoading()
            if(res.code === 0) {
              if(shelfStatus === 0) {
                this.$message.success("批量上架成功")
              }else if(shelfStatus === 1) {
                this.$message.success("批量下架成功")
              }
              this.doSearchHandel()
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            utils.clearLoading()
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      }
      
    }
  }
</script>


