<style>
  .ll-p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .sf-pre-goods-img-w {
    width: 110px;
    height: 60px;
  }
  .sf-pre-goods-img-w>img {
    width: 110px;
    height: 60px;
  }
  .ll-p-m-c-r {
    padding: 0 10px;
  }
  .ll-erp-p-no {
    color: #666;
    font-size: 12px;
  }

  .date-wrap-inline {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
  }

</style>
<template>
  <div>
    <div class="batch-handel-w">
      <el-button class="sup-mini" size="mini" icon="el-icon-upload2" @click="batchUpdateShelfStatus(0)" :disabled="checkedList.length === 0">上架</el-button>
      <el-button class="sup-mini" size="mini" icon="el-icon-download" @click="batchUpdateShelfStatus(1)" :disabled="checkedList.length === 0">下架</el-button>
      <el-button class="sup-mini" size="mini" icon="el-icon-delete" @click="doBatchDelHandel" :disabled="checkedList.length === 0">删除</el-button>
    </div>
    <el-table
      :data="tableData"
      empty-text="没有查询到您想要的数据"
      style="width:100%;"
      size="small"
      @select="handleSelect"
      @select-all="handleSelectAll">
      <el-table-column
        type="selection"
        align="center">
      </el-table-column>
      <el-table-column min-width="160px" label="ID">
        <template slot-scope="scope">
          <div class="ll-erp-p-no">活动ID: {{scope.row.activityId}}</div>
          <div class="ll-erp-p-no">活动商品ID: {{scope.row.activityPlatCityGoodsId}}</div>
          <div class="ll-erp-p-no">SKU编码：{{scope.row.goodsNo}}</div>
        </template>
      </el-table-column>
      <el-table-column label="门店信息" min-width="130px">
        <template slot-scope="scope">
          <div>
            <p>{{scope.row.storeName}}</p>
            <p class="erp-p-no">ERP编码：{{scope.row.storeNo}}</p>
          </div>
        </template>  
      </el-table-column>
      <el-table-column min-width="280px" label="商品信息">
        <template slot-scope="scope">
          <div class="ll-p-m-w">
            <div class="sf-pre-goods-img-w">
              <img v-if="scope.row.preSaleImageUrl && scope.row.preSaleImageUrl != ''" :src="scope.row.preSaleImageUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
              <img v-else src="/shop/static/img_error.png" alt="">
            </div>
            <div class="ll-p-m-c-r">
              <div>
                {{scope.row.goodsName || '-'}}
              </div>
              <div class="ll-erp-p-no">商品编码：{{scope.row.skuNo || '--'}}</div>
              <div class="ll-erp-p-no">区域商品ID：{{scope.row.platformCityGoodsId || '--'}}</div>
            </div>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="spec" label="规格" min-width="90px"></el-table-column>
      <el-table-column prop="price" label="原价" min-width="90px"></el-table-column>
      <el-table-column prop="preSalePrice" label="预售价" min-width="90px"></el-table-column>
      <el-table-column min-width="120px" prop="activityName" label="预售活动名称"></el-table-column>
      <el-table-column min-width="90px" prop="presaleTypeName" label="预售类型"></el-table-column>
      <el-table-column min-width="90px" prop="cycleTypeName" label="周期类型"></el-table-column>
      <el-table-column min-width="210px" label="有效期">
        <template slot-scope="scope">
          <div class="date-wrap-inline" v-if="scope.row.activityStartDate && scope.row.activityEndDate">
            <div>
              <p>{{scope.row.activityStartDate.substr(0, 10)}}</p>
              <p>{{scope.row.activityStartDate.substr(11, 8)}}</p>
            </div>
            <div>&nbsp;-&nbsp;</div>
            <div>
              <p>{{scope.row.activityEndDate.substr(0, 10)}}</p>
              <p>{{scope.row.activityEndDate.substr(11, 8)}}</p>
            </div>
          </div>
        </template>
      </el-table-column>
      <el-table-column min-width="130px" label="截单时间">
          <template slot-scope="scope">
            <div>
              {{scope.row.cutoffTypeName}} {{scope.row.cutoffTime}}
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="110px" prop="pickUpDate" label="提货日期"></el-table-column>
      <el-table-column min-width="60px" prop="shelfStatus" label="状态" :formatter="shelfStatusFormatter"></el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        width="100">
        <template slot-scope="scope" size="small">
          <div>
            <el-button type="text" size="mini" v-if="scope.row.shelfStatus === 1" @click="updateShelfStatus(scope.row, '0')" icon="el-icon-upload2">上架</el-button>
            <el-button type="text" size="mini" v-else-if="scope.row.shelfStatus === 0" @click="updateShelfStatus(scope.row, '1')" icon="el-icon-download">下架</el-button>
          </div>
          <div>
            <el-button type="text" size="mini" icon="el-icon-delete" @click="deleteItem(scope.row)">删除</el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import * as utils from '@/utils/utils'
export default {
  data() {
    return {
      tableData: [],
      // 选中的商品
      checkedList: []
    }
  },
  methods: {
    /**
     * [setDefaultMesg 设置信息]
     * @param {[type]} _val [description]
     */
    setDefaultMesg(_val) {
      this.tableData = _val.tableData
      this.checkedList = []
    },
    /**
     * [shelfStatusFormatter 格式化状态]
     */
    shelfStatusFormatter(row, column, cellValue, index) {
      if(cellValue === 0) {
        return '上架'
      }else if(cellValue === 1) {
        return '下架'
      } 
    },
    /**
     * [updateShelfStatus 上下架]
     * @param  {[type]} _row    [当前行信息]
     * @param  {[type]} _shelfStatus [状态  0  上架  1  下架]
     * @return {[type]}         [description]
     */
    updateShelfStatus(_row, _shelfStatus) {
      let _value = {
        row: _row,
        shelfStatus: _shelfStatus
      }

      this.$emit('updateShelfStatus', _value)
    },
    /**
     * [deleteItem 删除操作]
     * @param  {[type]} _row [description]
     * @return {[type]}      [description]
     */
    deleteItem(_row) {
      let _value = {
        row: _row
      }
      this.$emit('deleteItem', _value)
    },
    /**
     * [handleSelect 选择某一个]
     * @return {[type]} [description]
     */
    handleSelect(val, row) {
      let hash = {}
      let checkedListTemp = []

      if(utils.isKeyInArrayBykey(row.id, 'id', this.checkedList)) {
        this.checkedList = utils.removeItemByKeyVal('id', row.id, this.checkedList)
        return
      }else {
        checkedListTemp.push(row)
      }

      let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
      this.checkedList = checkedListTempArr.reduceRight((item, next) => {
        hash[next.id] ? '' : hash[next.id] = true && item.push(next)
        return item
      }, [])
    },
    /**
     * [handleSelectAll 选择多个]
     * @return {[type]} [description]
     */
    handleSelectAll(val) {
      let hash = {}
      if(val.length > 0) {
        // 全选
        for(let item of val) {
          this.checkedList.push(item)
        }
        // 去重
        this.checkedList = this.checkedList.reduceRight((item, next) => {
          hash[next.id] ? '' : hash[next.id] = true && item.push(next)
          return item
        }, [])
      }else { 
        // 全不选 此时val.length<=0
        // 1.获取当前table中每一条的id
        let checkedListTemp = []
        for(let item of this.tableData) {
          checkedListTemp.push(item)
        }
        // 2.从要传递的参数中去掉这些重复的
        let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'id')
        this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
      }
    },
    /**
     * [doBatchDelHandel 批量删除]
     * @return {[type]} [description]
     */
    doBatchDelHandel() {
      let _value = {
        checkedList: this.checkedList
      }

      this.$emit('doBatchDelHandel', _value)
    },
    /**
     * [batchUpdateShelfStatus 批量上下架]
     * @param  {[type]} _shelfStatus [description]
     * @return {[type]}              [description]
     */
    batchUpdateShelfStatus(_shelfStatus) {
      let _value = {
        shelfStatus: _shelfStatus,
        checkedList: this.checkedList
      }
      this.$emit('batchUpdateShelfStatus', _value)
    }

  }
}
</script>



